.navbar{width:100%;height:50px}.nav-border{width:100%;border-bottom:1px solid #f0f0f5}.personal-links{width:100%;height:100%;display:flex;justify-content:space-evenly;align-items:center}.personal-links a{width:fit-content;height:100%;text-decoration:none;padding-top:20px;font-size:24px;color:black}.personal-links a i{display:flex;width:100%;height:100%}.personal-links a i .github{margin-left:10px;font-family:SFMono-Regular,Consolas,Liberation}.personal-links a i .linkedin{margin-left:10px;font-family:sans-serif;font-weight:500}.personal-links a:hover{color:grey}#instruction-background{position:fixed;top:0;bottom:0;left:0;right:0;display:none;justify-content:center}.instruction-container-top{margin-top:160px;margin-right:0;width:100%;height:300px;display:flex;justify-content:center}.instruction-content{margin-right:0;width:400px;height:300px;display:flex;flex-direction:column;align-items:center;padding:0 20px;border:1px solid red;background-color:white;font-family:sans-serif;font-weight:700;text-align:center}.instruction-content h3{font-size:24px;padding:20px}.instruction-content p{font-size:20px}.instruction-content #x{width:100%;display:flex;padding-top:12px;color:#ff8585}#body-container{display:flex;width:100%;height:100vh;padding-top:50px;background-image:url(images/building.png);background-repeat:no-repeat;background-size:cover}#score{width:100px;height:100px;font-family:sans-serif;font-weight:700;padding-right:10%}.side-container{margin-left:5%;width:20%;height:80%;font-family:sans-serif;font-weight:700;font-size:24px;color:#585858;opacity:90%;box-shadow:rgba(0,0,0,0.35) 0px 5px 15px;padding:16px}.side-container .side-container-content{width:100%;height:100%;background-color:white;display:flex;flex-direction:column;align-items:center}.side-container p{text-align:center;padding-top:20px}.side-container p:hover{text-decoration:underline}.side-container #start{background-color:#fff;border:1px solid pink;padding:10px 20px;margin-bottom:50px;border-radius:4px;margin-top:50px}.side-container #start:hover{background-color:pink;border:1px solid #ffadba;padding:10px 20px;margin-bottom:50px}.side-container #start:focus{text-decoration:none;outline:none}.game-container{margin-left:10%;display:flex;flex-direction:column;justify-content:center;height:80%;box-shadow:rgba(0,0,0,0.35) 0px 5px 15px;padding:16px}#start-game-page{position:fixed;top:0;bottom:0;left:0;right:0;display:flex;justify-content:center}.start-game-container{margin-top:200px;margin-right:0;max-width:400px;height:400px;display:flex;flex-direction:row;justify-content:center;align-items:center}.start-game-text{background-color:#facfcf;width:100%;height:100%;display:flex;align-items:center;text-align:center;font-family:sans-serif;font-weight:700;font-size:36px;color:#fff;box-shadow:rgba(253,113,113,0.493) 0px 0px 10px 5px;padding:16px;margin:60px 0;border-radius:15px;animation:shake 1s;animation-iteration-count:infinite}@keyframes shake{0%{transform:translate(1px, 1px) rotate(5deg)}20%{transform:translate(-3px, 0px) rotate(10deg)}30%{transform:translate(3px, 2px) rotate(-10deg)}50%{transform:translate(-1px, 2px) rotate(-20deg)}70%{transform:translate(3px, 1px) rotate(-25deg)}90%{transform:translate(1px, 2px) rotate(10deg)}100%{transform:translate(1px, -2px) rotate(-10deg)}}body{width:100%;height:100%}canvas{background:#eee;width:auto;height:600px;opacity:90%}
